px 0 1px -10px;\n  border-radius: 5px;\n}\n\n.shape-list .selected-row {\n  background-color: #337ab7;\n  color: white;\n}\n\n.shape-list .indent {\n  margin-left: 30px;\n  padding-bottom: 5px;\n}\n\n/**\n * The shape-action-icons class is applied to the icons that are to the right of a shape's name in\n * the shape tree on the left side of the app.\n***/\n\n.action-icons {\n  padding-left: 10px;\n  font-size: .8em;\n}\n\n.action-icons i {\n  padding-left: 5px;\n  cursor: pointer;\n}\n\n.expand-icon {\n  cursor: pointer;\n}\n\n.myselectable {\n  cursor: default;\n}\n\n.non-editable-text {\n  cursor: default;\n}\n\n.editable-text {\n  cursor: text;\n}\n\n/* The editable-click class is originally defined in the X-Editable library. We don't like the \ndashed bottom border it adds, so we'll override here. */\n\n.editable-click {\n  border-bottom: none;\n}\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]